.chip {
  display: inline-flex;
  border-radius: 4px;
  border: solid 1px #e2e2ec;
  font-size: 14px;
  background-color: #fbfbff;
  color: #202126;
  padding: 2.5px;
  align-items: center;

  .chip-button-close {
    display: flex;
      
    i {
      font-size: 14px;
      display: flex;
    }
  }
}
